  color: #00AAFF;
}

/* Admin Dashboard Stats Cards Improvements */
.admin-card {
  cursor: default;
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.admin-card.clickable {
  cursor: pointer;
  position: relative;
}

.admin-card.clickable::after {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: transparent;
  z-index: 1;
}

.admin-card.clickable .card-action-button {
  position: relative;
  z-index: 2;
}

.admin-card-header h2 {
  margin: 0;
  color: #000000;
}

.admin-card-content p {
  margin: 0 0 5px 0;
}

/* Quick Actions Section */
.admin-actions {
  margin-top: 40px;
}

.admin-actions h2 {
  font-size: 1.8rem;
  font-weight: 600;
  margin-bottom: 15px;
  color: #000000;
}

.admin-action-buttons {
  display: flex;
  flex-wrap: wrap;
  gap: 10px;
}

.admin-action-button {
  background-color: #00AAFF;
  color: white;
  border: none;
  border-radius: 25px;
  padding: 10px 15px;
  font-size: 0.9rem;
  font-weight: 600;
  text-decoration: none;
  transition: all 0.3s ease;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
}

.admin-action-button:hover {
  background-color: #0088cc;
  transform: translateY(-3px);
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.15);
}

/* Utilities Section */
.admin-utilities {
  margin-top: 40px;
  padding-top: 20px;
  border-top: 1px solid #eee;
}

.admin-utilities h2 {
  font-size: 1.8rem;
  font-weight: 600;
  margin-bottom: 15px;
  color: #000000;
}

.admin-utilities-buttons {
  display: flex;
  gap: 10px;
  margin-bottom: 15px;
}

.utility-button {
  padding: 10px 15px;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  font-weight: 600;
  transition: all 0.2s ease;
}

.utility-button.danger {
  background-color: #dc3545;
  color: white;
}

.utility-button.danger:hover {
  background-color: #c82333;
  transform: translateY(-2px);
}

.utility-warning {
  color: #721c24;
  background-color: #f8d7da;
  padding: 10px;
  border-radius: 4px;
  font-size: 0.9rem;
  margin-top: 10px;
}

/* Responsive improvements */
@media (max-width: 768px) {
  .admin-stats {
    grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
  }
  
  .admin-action-buttons {
    flex-direction: column;
  }
  
  .admin-action-button {
    width: 100%;
    text-align: center;
  }
}
